<template>
	<div class="user_invitation">
      <van-nav-bar
        title="提现申请"
        left-text="返回"
        right-text=""
        left-arrow
        @click-left="goBack"
      />
		<div class="invitation_wrap">
      <van-cell-group>
        <van-field v-model="money" label="提现金额" placeholder="请输入金额" />
        <van-field v-model="password" label="登录密码" type="password" placeholder="请输入登录密码" />
        <van-cell title="收款方式：转账"></van-cell>
      </van-cell-group>
      <div class="intro">
<!--        <ul type="square">-->
<!--          <li>提现将于1-2个工作日后完成</li>-->
<!--          <li style="color:red;">一天最多提现{{conf.deposit_day_num}}次，一个月最多提现{{conf.deposit_month_num}}次</li>-->
<!--          <li style="color:red;">未满50元提现，扣除2元手续费；满50元提现，无手续费</li>-->
<!--        </ul>-->
      </div>
		</div>
		<van-button @click="submit" class="bottom_btn" type="primary"  bottomAction>确认提现</van-button>
	</div>
</template>

<script>

  import {CellGroup, Dialog, Row, Col, Radio, RadioGroup, Toast} from 'vant';
  import {WITHDRAW,USER_PROFILE,CONF} from '@/api/user';

	export default {
	  name : 'withdraw',
    data(){
	    return{
        payment:1,
        password : "",
        money:'',
        weixin:'',
        bank:'',
        cardno:'',
        user_type: "",
        conf: ""
      }
    },
    props: {
	    from : String
    },
    created(){
      this.initPage();
		},
    methods : {
      initPage(){
			  // let _this = this;
        // _this.$user.profile(function(data) {
        //     _this.user_type = data.user_type
        // });
        // this.$reqGet(CONF)
        // .then(res => {
        //    _this.conf = res.data.data
        // }).catch((err) => {
        // })
      },
	    submit : function(){
        if(!this.money) {
          Toast.fail('请输入提现金额');
          return;
        }
        this.$reqPost(WITHDRAW, {
          money: this.money,
          password: this.password,
          methods: this.payment
        }).then((res) => {
          this.submitSuccess(res)
        }).catch((err) => {
        });
      },
      submitSuccess(res){
        let result = res.data;
        if(result.code!=0){
          Toast.fail(result.msg);
          return;
        }
        Dialog.alert({
          title: "提示",
          message: "提现预计会在24小时内完成（若超时未到账请联系好评返现微信客服）"
        }).then(()=>{
          this.$router.push({path: "/user/withdraw/list"});
        })
      },
      //返回
      goBack() {
       this.$router.go(-1)
      }
    },
    components:{
      [CellGroup.name] : CellGroup,
      [Row.name] : Row,
      [Col.name] : Col,
      [RadioGroup.name] : RadioGroup,
      [Radio.name] : Radio,
      [Toast.name]: Toast
    }

	}
</script>

<style lang="scss" scoped>
	.invitation_wrap{
		height: 100%;
    background-color: #fff;
		box-sizing: border-box;
	}
	.bottom_btn{
		bottom: 0;
	}

  .intro{
    margin: 30px;
    padding: 0px 30px;
    border: 1px solid #f2f2f2;
    ul{
      list-style-type:square;
    }
    li {
      margin: 10px;
    }
  }
</style>



